#page-create-point {
    width: 100%;
    max-width: 1100px;
  
    margin: 0 auto;
  }
  .detailContainer{
    display: flex;
    margin-bottom: 32px;
    border-radius: 8px;
    background-color: #F0F0F5;
    justify-content: space-around;
  }
  .detailContainer .list{
    display: flex;
    text-align: center;
    margin: 20px;
    align-self: center;
  }

  .detailContainer img{
    height: 125px;
    width: 250px;
    display: flex;
    align-self: center;
    margin: 20px;
    margin-right: 0px;
    border-radius: 10px;
  }

  .detailContainer li{
    list-style: none;
    /* margin-bottom: 10px; */
    font-family: 'Roboto_400Regular';
    font-size: 18px;
  }

  .detailContainer ul{

    list-style: none;
    
    
  }
  #page-create-point header {
    margin-top: 48px;
    padding: 12px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  #page-create-point header a {
    color: var(--title-color);
    font-weight: bold;
    text-decoration: none;
  
    display: flex;
    align-items: center;
  }
  
  #page-create-point header a svg {
    margin-right: 16px;
    color: var(--primary-color);
  }
  
  #page-create-point form {
    margin: 80px auto;
    padding: 24px;
    max-width: 730px;
    background: #FFF;
    border-radius: 8px;
  
    display: flex;
    flex-direction: column;
  }
  
  #page-create-point form h1 {
    font-size: 36px;
  }
  
  #page-create-point form fieldset {
    margin-top: 32px;
    /* margin-bottom: 64px; */
    min-inline-size: auto;
    border: 0;
  }
  
  #page-create-point form legend {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 40px;
  }
  
  #page-create-point form legend h2 {
    font-size: 24px;
  }
  
  #page-create-point form legend span {
    font-size: 14px;
    font-weight: normal;
    color: var(--text-color);
  }
  
  #page-create-point form .field-group {
    display: flex;

    flex-direction: column;
  }
  
  #page-create-point form .field {
    flex: 1;
  
    display: flex;
    flex-direction: column;
    margin-bottom: 24px;
  }
  
  #page-create-point form .field input[type=text],
  #page-create-point form .field input[type=email],
  #page-create-point form .field input[type=number] {
    flex: 1;
    background: #F0F0F5;
    border-radius: 8px;
    border: 0;
    padding: 16px 24px;
    font-size: 16px;
    color: #6C6C80;
  }
  
  #page-create-point form .field select {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    flex: 1;
    background: #F0F0F5;
    border-radius: 8px;
    border: 0;
    padding: 16px 24px;
    font-size: 16px;
    color: #6C6C80;
  }
  
  #page-create-point form .field input::placeholder {
    color: #A0A0B2;
  }
  
  #page-create-point form .field label {
    font-size: 14px;
    margin-bottom: 8px;
  }
  
  #page-create-point form .field :disabled {
    cursor: not-allowed;
  }
  
  #page-create-point form .field-check {
    flex-direction: row;
    align-items: center;
  }
  
  #page-create-point form .field-check input[type=checkbox] {
    background: #F0F0F5;
  }
  
  #page-create-point form .field-check label {
    margin: 0 0 0 8px;
  }
  
  #page-create-point form .leaflet-container {
    width: 100%;
    height: 350px;
    border-radius: 8px;
    margin-bottom: 24px;
  }
  
  #page-create-point form button {
    width: 260px;
    height: 56px;
    background: var(--primary-color);
    border-radius: 8px;
    color: #FFF;
    font-weight: bold;
    font-size: 16px;
    border: 0;
    align-self: flex-end;
    margin-top: 40px;
    transition: background-color 0.2s;
    cursor: pointer;
  }
  
  #page-create-point form button:hover {
    background: #2FB86E;
  }
  
  .items-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 16px;
    list-style: none;
  }
  
  .items-grid li {
    background: #f5f5f5;
    border: 2px solid #f5f5f5;
    height: 180px;
    border-radius: 8px;
    padding: 32px 24px 16px;
  
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
  
    text-align: center;
  
    cursor: pointer;
  }
  
  .items-grid li span {
    flex: 1;
    margin-top: 12px;
  
    display: flex;
    align-items: center;
    color: var(--title-color)
  }
  
  .items-grid li.selected {
    background: #E1FAEC;
    border: 2px solid #34CB79;
  }